import BaseApiService from "services/BaseApiService";

const DEFAULT_PATH = "/custom/api/stage/manage/partner";

class PartnerService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/pipeline/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new PartnerService();
